import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat',sans-serif
    
}

html, body{
    position: relative;
    background: #232427; 
}
`;

export default GlobalStyle;
